import React , {useState , useRef, useEffect }from 'react'
import Loader from '../../common/loader/Loader'
import { ToastContainer } from 'react-toastify'

import {
  Box,
  Button,
  Card,
  CardActionArea,
  TextField,
  CardContent,
  Grid,
  Typography,
  IconButton,
    CircularProgress,
    Paper,
  Chip,
  MenuItem,
  Stack,
  Autocomplete,
} from '@mui/material';
import { CheckCircle } from '@mui/icons-material';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import { getAllBank , singlePayout, validatePayoutLinkToken } from '../../api/login/Login';
import withLocation from '../../utils/withLocation';
import toastMessage from '../../utils/toastMessage';
import { useParams } from 'react-router-dom';

const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    InputLabelProps={{
      shrink: true,
    //   style: { fontSize: '16px' },
    }}
    sx={{
    
      "& label.Mui-focused": {
        color: 'var(--bs-nav)',
      },
        "& .MuiOutlinedInput-root": {
            height: 40, 
            "& .MuiOutlinedInput-input": {
                py: 0,
              },
          "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
          "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
        },
     
    }}
    {...props}
  />
);


const CustomBox =  ({children})=> (
    <Box
    sx={{
      width: '100%',
    //   maxWidth: 800,
      mx: 'auto',
    //   mt: 5,
    my:1,
      px: 3,
      py:0,
      pb:6,
    //   bgcolor: '#F8F9FA',
    bgcolor:'white',
      borderRadius: 1,
      boxShadow: 0,
    }}
  >
    {children}
    </Box>
)


const PayoutForm = () => {
  const { loader ,  setLoader } = useState();
   const [bankList, setBankList] = useState([]);
   const [formValidator , setFormValidater] = useState(false)
    const [formValidatorLoader , setFormValidaterLoader] = useState(true)
   const [validateMessage , setValidateMessage] = useState('')
  const { id }= useParams()

  

  const [formData, setFormData] = useState({
    customer_mobile: '',
    receiver_name: '',
    ifsc_code: '',
    bank_name: null,
    account_number: '',
    sender_name: '',
    amount: '',
    remarks: '',
    account_type: '',
    txn_mode: '',
    beneficiary_email: '',
  });

  const [formErrors, setFormErrors] = useState({});

const handleChange = (e) => {
  const { name, value } = e.target;

  const trimmedValue = value.trimStart(); // prevent leading spaces

  if (name === 'amount') {
    const regex = /^\d*\.?\d{0,2}$/;
    if (trimmedValue === '' || regex.test(trimmedValue)) {
      setFormData((prev) => ({ ...prev, [name]: trimmedValue }));
    }
  } else if (name === 'customer_mobile' || name === 'account_number') {
    const cleanedValue = trimmedValue.replace(/\D/g, '');
    setFormData((prev) => ({ ...prev, [name]: cleanedValue }));
  } else {
    setFormData((prev) => ({ ...prev, [name]: trimmedValue }));
  }

  setFormErrors((prev) => ({ ...prev, [name]: '' }));
};


const validate = () => {
  const errors = {};

  if (!formData.customer_mobile || formData.customer_mobile.trim().length !== 10) {
    errors.customer_mobile = 'Enter valid 10-digit mobile number';
  }

  if (!formData.receiver_name.trim()) {
    errors.receiver_name = 'Receiver name is required';
  }

  if (!formData.bank_name) {
    errors.bank_name = 'Bank name is required';
  }

  if (!formData.account_number.trim()) {
    errors.account_number = 'Account number is required';
  }

  if (!formData.amount || isNaN(formData.amount)) {
    errors.amount = 'Enter a valid amount';
  }

  if (!formData.account_type) {
    errors.account_type = 'Select account type';
  }

  if (!formData.txn_mode) {
    errors.txn_mode = 'Select transaction mode';
  }

  if (formData.beneficiary_email.trim()) {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.beneficiary_email.trim())) {
      errors.beneficiary_email = 'Enter a valid email';
    }
  }

    const ifscRegex = /^[A-Z]{4}0[A-Z0-9]{6}$/;
  if (!formData.ifsc_code || !formData.ifsc_code.trim()) {
    errors.ifsc_code = 'IFSC code is required';
  } else if (!ifscRegex.test(formData.ifsc_code.trim().toUpperCase())) {
    errors.ifsc_code = 'Enter a valid IFSC code';
  }

  setFormErrors(errors);
  return Object.keys(errors).length === 0;
};


 const handleSubmit = withLocation(async (latitude, longitude, e) => {
  

  if (validate()) {
    const payload = {
      ...formData,
      latlong: `${latitude.toFixed(4)},${longitude.toFixed(4)}`,
      bank_name : formData.bank_name.name
    };

    try {
      setLoader(true);
      const res = {}
    //   const res = await singlePayout(payload); 
      // handle success response
       toastMessage(res?.data?.error , res?.data?.message)
      if(res?.data?.error === false)
      {
         setFormData(
          {
    customer_mobile: '',
    receiver_name: '',
    ifsc_code: '',
    bank_name: null,
    account_number: '',
    sender_name: '',
    amount: '',
    remarks: '',
    account_type: '',
    txn_mode: '',
    beneficiary_email: '',
  }
         )
      }

    } catch (error) {
      console.error('Payout error:', error);
    } finally {
      setLoader(false);
    }
  }
});



const handleFormSubmit = (e) => {
  e.preventDefault(); 
  handleSubmit(e);    
}


    useEffect(() => {
    const fetchBanks = async () => {
      try {
        const res = await getAllBank(); // Call your API
        const mapped = res?.data?.data?.map((item) => ({
          value: item,
          label: item.bank_name,
          _id: item._id,
          name: item.bank_name, // for rendering
        }));
        setBankList(mapped);
      } catch (error) {
        console.error("Error fetching bank list:", error);
      }
    };
    fetchBanks();
  }, []);

   const handleBankChange = (event, newValue) => {
   
    setFormData((prev) => ({
      ...prev,
      bank_name: newValue,
    }));
    setFormErrors((prev) => ({ ...prev, bank_name: '' }));
  };


  const checkValidateForm = async()=> {
    setFormValidaterLoader(true)
    try {
        let res = await validatePayoutLinkToken(id) 
        if(res?.data?.error === false && res?.data?.statusCode === 200)
        {
            setFormValidater(true)
        }
        else{
           setFormValidater(false) 
           setValidateMessage(res?.data?.message)
        }
    } catch (error) {
        
    } finally {
        setFormValidaterLoader(false)
    }
  }

  useEffect(()=>{
checkValidateForm()
  },[])

    if(formValidatorLoader)
    {
        return <Loader/>
    }

    if(formValidator === false && validateMessage)
        return <p>{validateMessage}</p>

  return (
    <>
      
    <Box sx={{maxWidth:'800px' , m:'auto' , mt:3}}>

    
      <div className="PageHeading">
        <h1>Payout Service</h1>
      </div>

      <CustomBox>

        <Box component="form" onSubmit={(e) => handleFormSubmit(e)}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Customer Mobile"
                name="customer_mobile"
                value={formData.customer_mobile}
                onChange={handleChange}
                required
                 inputProps={{ maxLength: 10 }}
                error={!!formErrors.customer_mobile}
                helperText={formErrors.customer_mobile}
                size="small"
              
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Receiver Name"
                name="receiver_name"
                value={formData.receiver_name}
                onChange={handleChange}
                required
                error={!!formErrors.receiver_name}
                helperText={formErrors.receiver_name}
                size="small"
              />
            </Grid>

        <Grid item xs={12} sm={6}>
  <Autocomplete
    options={bankList}
    value={formData.bank_name}
    onChange={handleBankChange}
    getOptionLabel={(option) => option?.label || ''}
    isOptionEqualToValue={(option, value) => option?._id === value?._id}
    fullWidth
    size="small"
    renderOption={(props, option) => (
      <li {...props} key={option._id}>
        <Box
          sx={{ fontSize: '13px', color: 'var(--bs-nav)' }}
          display="flex"
          columnGap={1.5}
          alignItems="center"
        >
          {option.name}
        </Box>
      </li>
    )}
    renderInput={(params) => (
     <CustomTextField
    {...params}
    required
    label="Bank Name" // 
    error={!!formErrors.bank_name}
    helperText={formErrors.bank_name}
    fullWidth
    size="small"
     InputLabelProps={{ shrink: true }} // ✅ shrink enabled
  />
    )}
  />
</Grid>

<Grid item xs={12} sm={6}>
  <CustomTextField
    fullWidth
    label="IFSC Code"
    name="ifsc_code"
    value={formData.ifsc_code || ''}
    onChange={handleChange}
    required
    size="small"
    error={!!formErrors.ifsc_code}
                helperText={formErrors.ifsc_code}
  />
</Grid>


            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Account Number"
                name="account_number"
                value={formData.account_number}
                onChange={handleChange}
                required
                error={!!formErrors.account_number}
                helperText={formErrors.account_number}
                size="small"
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Sender Name"
                name="sender_name"
                value={formData.sender_name}
                onChange={handleChange}
                size="small"
                required
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Amount"
                name="amount"
                type="text"
                value={formData.amount}
                onChange={handleChange}
                required
                error={!!formErrors.amount}
                helperText={formErrors.amount}
                size="small"
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Remarks"
                name="remarks"
                value={formData.remarks}
                onChange={handleChange}
                multiline
                required
                size="small"
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                select
                fullWidth
                label="Account Type"
                name="account_type"
                value={formData.account_type}
                onChange={handleChange}
                size="small"
                required
                error={!!formErrors.account_type}
                helperText={formErrors.account_type}
              >
                <MenuItem sx={{color:'var(--bs-nav)' , fontSize:13}} value="1">Savings</MenuItem>
                <MenuItem sx={{color:'var(--bs-nav)' , fontSize:13}} value="2">Current</MenuItem>
              </CustomTextField>
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                select
                fullWidth
                label="Transaction Mode"
                name="txn_mode"
                value={formData.txn_mode}
                onChange={handleChange}
                size="small"
                required
                error={!!formErrors.txn_mode}
                helperText={formErrors.txn_mode}
              >
                <MenuItem sx={{color:'var(--bs-nav)' , fontSize:13}} value="imps">IMPS</MenuItem>
                <MenuItem sx={{color:'var(--bs-nav)' , fontSize:13}} value="neft">NEFT</MenuItem>

              </CustomTextField>
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Beneficiary Email"
                name="beneficiary_email"
                value={formData.beneficiary_email}
                onChange={handleChange}
                size="small"
                required
                error={!!formErrors.beneficiary_email}
                helperText={formErrors.beneficiary_email}
              />
            </Grid>
          </Grid>

          <Box mt={4} textAlign="center">
            <Button
              type="submit"
              variant="contained"
              sx={{
                backgroundColor: 'var(--bs-primary1)',
                color: '#fff',
                textTransform: 'none',
                fontWeight: 600,
                width:'200px',
                fontSize: 14,
                '&:hover': {
                  backgroundColor: 'var(--bs-primary1)',
                  opacity: 0.9,
                },
              }}
            >
              Proceed
            </Button>
          </Box>
        </Box>
      </CustomBox>
      </Box>
    </>
  );
};

export default PayoutForm